@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&family=Ubuntu+Mono&family=Ubuntu:wght@500&display=swap");

@import url("https://fonts.googleapis.com/css2?family=Roboto+Mono:wght@300&family=Share+Tech+Mono&family=Ubuntu+Mono&family=Ubuntu:wght@500&display=swap");

$nav_height: 61px;
.nav_main_wrapper {
    user-select: none;
    height: $nav_height;

    // position: fixed;
    // top: 0;
    // left: 0;

    .nav_content {
        display: flex;
        justify-content: space-between;
        align-items: center;

        .logo_wrapper {
            width: 50px;
            height: 50px;
            display: flex;
            justify-content: center;
            align-items: center;
            position: relative;
            margin: 0 20px;
        }
        $breakpoint: 739px;

        .nav_links_wrapper {
            display: flex;
            align-items: center;
            justify-content: space-between;
            justify-self: center;
            padding: 0 25px;
            padding-right: 125px;

            @media screen and (max-width: 940px) {
                & {
                    padding-right: 54px;
                }
            }

            @media screen and (max-width: 940px) {
                & {
                    padding-right: 0;
                }
            }

            @media screen and (max-width: $breakpoint) {
                & {
                    padding: 0 0;
                }
            }

            @media screen and (max-width: 570px) {
                & {
                    display: none;
                }
            }

            .nav_link {
                margin: 0 15px;
                font-family: "Roboto Mono", monospace;
                font-family: "Ubuntu", sans-serif;
                font-family: "Ubuntu Mono", monospace;
                font-style: normal;
                font-weight: 400;
                font-size: 19px;
                text-align: center;
                letter-spacing: -0.005em;

                color: rgba(232, 232, 232, 0.874);

                &.active {
                    text-decoration: underline;
                    // background: red;
                }
            }
        }

        .nav_content_right {
            display: flex;
            align-items: center;
            justify-content: space-between;

            .auth_links {
                display: flex;
                align-items: center;
                justify-content: space-between;

                margin-left: auto;

                box-sizing: border-box;
                padding: 0 20px;

                @media screen and (max-width: $breakpoint) {
                    & {
                        display: none;
                    }
                }

                .auth_link {
                    margin: 0 10px;
                    font-family: "Roboto Mono", monospace;
                    font-family: "Share Tech Mono", monospace;
                    font-family: "Ubuntu", sans-serif;
                    font-family: "Ubuntu Mono", monospace;
                    font-style: normal;
                    // font-weight: 400;
                    font-size: 17px;
                    // line-height: 28px;
                    text-align: center;
                    letter-spacing: -0.005em;

                    &.active {
                        text-decoration: underline;
                    }
                }
            }

            .nav_content_right__end {
                display: flex;
                align-items: center;
                justify-content: space-between;
                .label {
                    $width: 50px;
                    border-radius: 50%;
                    display: inline-flex;
                    justify-content: center;
                    align-items: center;
                    height: $nav_height;

                    .svg {
                        display: flex;
                        justify-content: center;
                        align-items: center;
                        position: absolute;
                        height: $width;
                    }
                    .select {
                        appearance: none;
                        width: 100%;
                        height: 100%;
                        display: flex;
                        flex-shrink: 0;
                        font-size: 0;
                        background-position: center;
                        background: transparent;
                        border: none;
                        border-radius: 50%;
                        z-index: 10;
                        direction: rtl;
                        cursor: pointer;

                        padding: 20px;

                        outline: none;

                        .option {
                            font-size: 24px;
                            background: #000;
                            color: white;
                            outline: none;
                            text-align: left;
                        }
                    }
                }

                .burger_wrapper_main {
                    box-sizing: border-box;
                    padding: 0 10px 0 0px;
                    cursor: pointer;
                    .burger_wrapper {
                        display: inline-flex;
                        justify-content: center;
                        align-items: center;
                        margin: 0 !important;
                        .burger {
                            margin-left: 5px;
                            display: inline-flex;
                            justify-content: center;
                            align-items: center;
                        }
                    }
                }
            }
        }
    }
}
