@import "./nullifications.scss";
@import "./common.scss";
@import "./animations/page_animations.scss";
@import "./themes/themes";

.loader_wrapper {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    background: rgba(0, 0, 0, 0.857);
    z-index: 20;
    box-sizing: border-box;
    padding-bottom: 20vh;
}

// scrollbar
$scrollbar_track_bg_color: #292929;
::-webkit-scrollbar {
    width: 10px;
    @media screen and (max-width: 600px) {
        width: 2px;
    }
}
::-webkit-scrollbar-track {
    background: $scrollbar_track_bg_color;
}
::-webkit-scrollbar-thumb {
    background: #888;
    border: 1px solid $scrollbar_track_bg_color;
    border-radius: 25px;
}
::-webkit-scrollbar-thumb:hover {
    background: rgb(112, 112, 112);
}
::-webkit-scrollbar-thumb:active {
    background: rgb(79, 79, 79);
}

